<template>
    <i :class="classes" :style="styles"></i>
</template>

<script type="text/babel">
    import {isColor} from '../../../utils/assist';

    export default {
        name: 'x-icon',
        props: {
            name: String,
            color: {
                validator(value) {
                    if (!value) return true;
                    return isColor(value);
                }
            },
            size: [Number, String]
        },
        computed: {
            classes() {
                return 'x-icon-' + this.name;
            },
            styles() {
                const style = {};

                if (!!this.size) {
                    style.fontSize = /(rem|rpx)$/.test(this.size)?this.size:((Number(this.size) / 20 )+ 'rem');
                }

                if (!!this.color) {
                    style.color = this.color;
                }

                return style;
            }
        }
    }
</script>

<style lang="less">
    @import '../../../styles/components/x-icons.less';
</style>



